.socials {
  display: flex;
  justify-content: flex-start;
  align-items: center;

  @media (max-width: 638px) {
    transform: translate(-11px, 0);
  }
}

.socials-facebook {
  position: relative;
  cursor: pointer;
  margin: 0 18px 0 0;
  padding: 0;

  @media (max-width: $max-tablet) {
    margin: 0 15px 0 0;
  }

  &::before {
    content: "";
    display: inline-block;
    vertical-align: middle;
    -webkit-mask: url("../img/facebook.svg");
    mask: url("../img/facebook.svg");
    -webkit-mask-size: cover;
    mask-size: cover;
    background-color: $socials-background;
    background-size: 26px 26px;
    width: 26px;
    height: 26px;
  }
}

.socials-facebook:hover {
  &::before {
    background-color: $social-fb-hover;
  }
}

.socials-facebook:active {
  &::before {
    background-color: $social-fb-active;
  }
}

